@import '../App.scss';

.container{
    position: fixed;
    width: 100%; height: 100%; max-height: 100%;
    overflow: hidden;
    display: flex;

    @media screen and (min-width: $screen_small){
        flex-direction: column;
    }

    @media screen and (min-width: $screen_large){
        flex-direction: row;
    }
}
