@import '../../App.scss';

$offset : -45deg;

.menu {
    @include unset();
    z-index: 0;
    position: absolute; top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 10px;
    @media (min-width: 0px){
        width: 160px; height: 160px;
    }
    @media (min-width: 700px){
        width: 180px; height: 180px;
    }
    @media (min-width: 1000px){
        width: 220px; height: 220px;
    }
}

.menu li { 
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    margin : unset; border:none;
    -webkit-clip-path : polygon(70% 50%, 100% 50%, 100% 30%, 70% 0%, 50% 0%,50% 30%);
    clip-path: url(#sector);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

    & a {
        display: block;
        width: 100%; height: 100%;
        cursor: unset;
        color: inherit;
    }

    
}

.menu.active{
    .menu-item{ 
        color : $dark-gray; top: 0; left : 0;
        &.top { background-color: darken($color: $primary-lighter, $amount: 5%); }        
        &.right { background-color: darken($color: $primary-lighter, $amount: 20%); }
        &.bottom { background-color: darken($color: $primary-lighter, $amount: 30%); }
        &.left { background-color: darken($color: $primary-lighter, $amount: 40%); }
        &:hover { cursor: pointer; }
    }
}

.menu-item{
    $duration : 300ms; $delay : 600ms;
    transition: background-color $duration ease-in calc(var(--n) * 100ms), 
    color $duration ease-in calc(var(--n) * 100ms),
    top $duration ease-in calc(var(--n) * 100ms),
    left $duration ease-in calc(var(--n) * 100ms);
    background-color: transparent;
    color: transparent;
    cursor: default;
}

.icon {
    position: absolute; right: 13%; top: 20%;
    transform: rotate(45deg);
    font-size: 15px; font-weight: 600;

    @media (min-width: 0px){
        right: 12%; top: 19%;
        font-weight: 500;
    }
    @media (min-width: 700px){
        right: 13%; top: 21%;
    }
    @media (min-width: 1000px){
        font-size: 18px;
        letter-spacing: 2px;
        right: 10.5%; top: 19%;
    }
}

@mixin slide($anim_name){
    &.selected{ animation: #{$anim_name} 200ms linear 0s; animation-fill-mode: both; }
}
.top    { transform: rotate(0deg   + $offset); @include slide(slideup); }
.right  { transform: rotate(90deg  + $offset); @include slide(slideright);}
.bottom { transform: rotate(180deg + $offset); @include slide(slidedown);}
.left   { transform: rotate(270deg + $offset); @include slide(slideleft);}
.button{
    width: 40px; height: 40px; 
    position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
    border-radius: 50%; background-color: $light-gray;
    &:hover{cursor: pointer;}
    // animation: pulse 2.5s linear 0s infinite;
    // &.active{ animation: none; }
}



@keyframes pulse{
    0% { background-color: $primary-lighter; }
    50% { background-color: $gray; }
    100% { background-color: $primary-lighter; }
}

$slideDistance : 15%;
@keyframes slidedown{
    0% { top: 0}
    100%{ top: $slideDistance }
}
@keyframes slideleft{
    0% { left: 0}
    100%{ left: -$slideDistance  }
}
@keyframes slideup{
    0% { top: 0}
    100%{ top: -$slideDistance  }
}
@keyframes slideright{
    0% { left: 0}
    100%{ left: $slideDistance  }
}