@import '../App.scss';


.navbar{
    background-color: $primary-light;
    display: flex;
    @media screen and (min-width : $screen_small){
        @include w_h(100%, $nav_dimension);
        justify-content: space-between;
        flex-direction: row;
    }

    @media screen and (min-width : $screen_large){
        @include w_h($nav_dimension, 100%);
        flex-direction: column;
    }
}

a {
    text-decoration: none;
}


.logo{
    
    @include w_h($nav_dimension, $nav_dimension);
    display: flex; justify-content: center; align-items: center; 
    background-color: transparent; color : white;
    font-size: 20px; letter-spacing: 3px; vertical-align: middle;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    & :nth-child(1){padding-bottom: 5.5px}

    @media screen and (max-width: $screen_large) { margin-left: 15px; }
}

.menu-button{
    @include w_h($nav_dimension, $nav_dimension);
    background-color: $light-gray;
    display: flex; justify-content: center; flex-direction: column;

    @media screen and (min-width : $screen_large){ display: none }

    & .bar{
        background-color: white;
        height: 4px; margin: 3px 12px;  border-radius: 5px;
    }
}

.nav-links{
    background-color: transparent; color:black;
    opacity: 1;
    display: flex; flex: 1;
    
    @media screen and (max-width : $screen_large){
        @include w_h(100%, $nav_dimension - 20px);
        position: absolute; top : $nav_dimension; left: 0%;
        justify-content: flex-end;
        margin-left: 100%; transition: margin-left 0.7s ease-out, opacity 0.5s ease;
        background-color: $primary-light;
        opacity: 0;
        
        &.active{
            margin-left: 0%;
            opacity: 1;
        }
    }

    @media screen and (min-width : $screen_large){
        @include w_h(100%, 100%);
        transition: none; position: static;
        margin-left: 0%;
        flex-direction: column;
        justify-content: center;
        padding-bottom: $nav_dimension;
    }
}

a {
    display: flex; justify-content: center; color : $light-gray; 
    border-radius: 5px; transition: color 0.4s ease-in;

    &:hover{ cursor: pointer; color: white; }

    @media screen and (min-width : $screen_small){ margin: auto 10px; }
    @media screen and (min-width : $screen_large){ margin: 5px 0px;  padding: 10px 5px; }
}