@mixin w_h($width, $height) { width: $width; height: $height; }

@mixin setPropByBreakpoints($property, $values) {
    @media screen and (min-width: 0px)      { #{$property}: nth($list: $values, $n: 1) }
    @media screen and (min-width: 400px)    { #{$property}: nth($list: $values, $n: 2) }
    @media screen and (min-width: 800px)    { #{$property}: nth($list: $values, $n: 3) }
    @media screen and (min-width: 1200px)   { #{$property}: nth($list: $values, $n: 4) }
}

@mixin marginLeftByWidth($sizes) {
    @media screen and (min-width: 0px)      { margin-left: nth($list: $sizes, $n: 1) }
    @media screen and (min-width: 400px)    { margin-left: nth($list: $sizes, $n: 2) }
    @media screen and (min-width: 800px)    { margin-left: nth($list: $sizes, $n: 3) }
    @media screen and (min-width: 1200px)   { margin-left: nth($list: $sizes, $n: 4) }
}

@mixin credentials() {
    margin : 0px 5vmin 5vmin 0px
    // @media screen and (min-width: 0px)      { margin: 0px 40px 40px 0px }
    // @media screen and (min-width: 500px)    { margin: 0px 60px 60px 0px }
    // @media screen and (min-width: 800px)    { margin: 0px 70px 70px 0px }
    // @media screen and (min-width: 1200px)   { margin: 0px 100px 50px 0px }
}

@mixin cred(){
    font-size: 3vmin;
    @media screen and (min-width: 0px)      { margin: 5vmin 0px; letter-spacing: 8px;  }
    @media screen and (min-width: 500px)    { margin: 20px 0px; letter-spacing: 12px;  }
    @media screen and (min-width: 800px)    { margin: 30px 0px; letter-spacing: 17px;  }
    @media screen and (min-width: 1200px)   { margin: 35px 0px; letter-spacing: 20px;  }
}

@mixin button(){
    padding : 6px 12px; border-radius: 5px;
    background-color : transparent;
    font-size: 1.5rem;
    transition: background-color 0.6s ease;
    &:hover{
        cursor: pointer; background-color: $gray;
    }
}

@mixin verticalText() {
    writing-mode: vertical-rl;
    text-orientation: upright;
}

@mixin unset {
    all: initial;
    * { all: unset; }
}