@import '../../App.scss';

.home{
    position: relative;
    @include w_h(100%, 100%); 
   
}

.salutation{
    @media screen and (min-width: 0px) { padding: 75px 5vmin 0vmin 5vw; }
    @media screen and (min-width: $screen_large) { padding: 40px 5vmin 0vmin 5vw; }
}


.greeting {
    position: relative; color : $primary-light;
    font-size: 4.5vmin; font-weight: 700;
    animation: slideRight 2s ease;
    @include setPropByBreakpoints(margin-left, 0px 10px 20px 30px);
}

.name {
    position: relative; color : $primary-lighter; left: 100%; 
    font-weight: 700;
    animation: slightSlideLeft 3s ease 0.5s; animation-fill-mode: forwards;
    // @include setPropByBreakpoints(font-size, 50px 60px 70px 80px);
    font-size: 10vmin;
    @include setPropByBreakpoints(margin-left, 60px 75px 100px 120px);
}

.pronun {
    position: relative; color : $lighter-gray; left: 100%;
    font-weight: 500;
    margin-top: 8px;
    animation: slideRight 2.5s ease-out 1.3s; animation-fill-mode: forwards;
    @include setPropByBreakpoints(font-size, 12px 16px 20px 20px);
    @include setPropByBreakpoints(margin-left, 40px 50px 80px 90px);
}

.credentials {
    @include credentials();
    position: absolute; bottom: 0; right: 0;
    text-align: right; color : $lighter-gray;

    & .cred {
        @include cred();
        position: relative;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

        &:nth-child(1){ animation: fadein 1.5s ease 1.5s; animation-fill-mode: backwards}
        &:nth-child(2){ animation: fadein 1.7s ease 2.2s; animation-fill-mode: backwards}
        &:nth-child(3){ animation: fadein 2s ease 3.2s; animation-fill-mode: backwards}
        &:nth-child(4){ animation: fadein 2.2s ease 3.5s; animation-fill-mode: backwards}   
    }
}

.exit{
    animation: exit 700ms ease-in 500ms 1 normal forwards;
}
@keyframes exit{
    from {opacity: 1; left: 0;}
    to {opacity: 0; left: -100%}
}

@keyframes slideLeft{
    from { left: 100%; opacity: 0; }
    to { left: 0%; opacity: 1; }
}

@keyframes slightSlideLeft{
    from { left: 50%; opacity: 0; }
    to { left: 0%; opacity: 1; }
}

@keyframes slideRight{
    from { left: -100%; opacity: 0; }
    to { left: 0%; opacity: 1; }
} 
@keyframes fadein{
    from {opacity: 0; bottom: 50px;}
    to {opacity: 1; bottom: 0px;}
}