.menubar-container{
    $space : 16px;
    width: calc(100% - #{$space}); height: calc(100% - #{$space});
    margin-left: calc(#{$space} / 2);
    margin-top: calc(#{$space} / 2);
    display: flex; flex-direction: column;
    justify-content: space-around; align-items: center;
}
.menubar {
    width:90%; height: 3px;
    background-color: white;
}